@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Oswald";
  src: url("../../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../../fonts/oswald/oswaldregular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Lato";
  src: url("../../fonts/lato/latoregular.woff2") format("woff2"), url("../../fonts/lato/latoregular.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  box-sizing: border-box;
  height: 100%;
}

body {
  height: 100%;
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  font-family: "Lato", "Arial", sans-serif;
  color: #000000;
  background-color: #ffffff;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-width: 320px;
  min-height: 100%;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1,
h2,
h3,
h4 {
  margin: 0;
}

p {
  margin: 0;
}

.button {
  display: inline-block;
  width: 100%;
  padding: 10px 0;
  font-family: "Oswald", "Arial", sans-serif;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  background-color: #68b738;
  cursor: pointer;
}

.title {
  font-size: 36px;
  line-height: 40px;
}
@media (min-width: 768px) {
  .title {
    font-size: 60px;
    line-height: 60px;
  }
}

.container {
  max-width: 1220px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 30px;
  }
}
@media (min-width: 1440px) {
  .container {
    padding: 0;
  }
}

.header {
  position: relative;
}

.header__container {
  margin-bottom: 20px;
}
@media (min-width: 1440px) {
  .header__container {
    padding-top: 55px;
  }
}

.header__navigation {
  display: flex;
  justify-content: space-between;
  margin-top: 13px;
  margin-bottom: 10px;
}

@media (min-width: 1440px) {
  .header--home:after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 50%;
    z-index: -1;
    background-color: rgba(104, 183, 56, 0.85);
    background-image: linear-gradient(to top, rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../../img/cat-background-desktop.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
}

.navigation {
  position: relative;
}

.navigation__list {
  position: absolute;
  top: 100%;
  right: -20px;
  left: -20px;
  z-index: 2;
  display: none;
  flex-direction: column;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .navigation__list {
    position: static;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    gap: 33px;
  }
}
@media (min-width: 1440px) {
  .navigation__list {
    background-color: transparent;
  }
}

.navigation__link {
  font-weight: 400;
  font-size: 20px;
  font-family: "Oswald", "Arial", sans-serif;
  color: #000000;
  text-transform: uppercase;
}
@media (min-width: 1440px) {
  .navigation__link {
    color: #ffffff;
  }
}

.navigation.is-open .navigation__list {
  display: flex;
}

@media (min-width: 768px) {
  .navigation__toggle {
    display: none;
  }
}

